import React from 'react';
import PropTypes from 'prop-types';
import RadioFilter from './finder__radio';
import CheckboxFilter from './finder__checkbox';
import SelectboxFilter from './finder__selectbox';
import AppliedFilters from '../query/finder__appliedfilters';
import ClearFilters from './finder__clear__filter';
import ToggleFilter from './finder__toggle';

/**
 * Predicate for whether a facet should be displayed, if it's dependent on
 * another facet being set.
 *
 * @param {object} facet The facet to potentially display.
 * @param {object} facetMap The facets currently set on the query.
 * @return {bool} - Has any dependency been met
 */
function dependencyMet(facet, facetMap) {
    const setFacets = Object.keys(facetMap);
    if (!facet.dependency || setFacets.indexOf(facet.meta) >= 0) {
        return true;
    } else {
        if (
            setFacets.indexOf(facet.dependency) >= 0 &&
            facetMap[facet.dependency] !== ''
        ) {
            return true;
        } else {
            return false;
        }
    }
}

/**
 * Component to update facet values in the query.
 *
 * @param {object} props React props.
 * @returns {object} - React component.
 */
function finder__filters(props) {
    return (
        <div className="finder__filters">
            <fieldset>
                <AppliedFilters
                    config={props.config}
                    query={props.query}
                    update={props.update}
                    clear={props.clear}
                />
            </fieldset>
            <fieldset className="finder__filter">
                <div className="finder__filters__heading">
                    <h2>{`Filter ${props.config.summariseAs.plural}`}</h2>
                    <div className="finder__filters__heading__btn-icon">
                        <ClearFilters clear={props.clear} query={props.query} />
                        <span
                            className="far fa-sliders-h icon"
                            aria-hidden="true"
                        />
                    </div>
                </div>

                {props.config.facetLabels.map(facet => {
                    if (dependencyMet(facet, props.query.facets)) {
                        switch (facet.type) {
                            case 'radio':
                                return (
                                    <RadioFilter
                                        key={facet.meta}
                                        facet={facet}
                                        query={props.query}
                                        responseFacet={
                                            props.response &&
                                            props.response.facets
                                                ? props.response.facets.filter(
                                                      funnelbackFacet =>
                                                          funnelbackFacet.name ===
                                                          facet.funnelbackName
                                                  )
                                                : []
                                        }
                                        update={props.update}
                                        mobile={props.mobile ? true : false}
                                    />
                                );
                            case 'checkbox':
                                return (
                                    <CheckboxFilter
                                        key={facet.meta}
                                        facet={facet}
                                        query={props.query}
                                        responseFacet={
                                            props.response &&
                                            props.response.facets
                                                ? props.response.facets.filter(
                                                      funnelbackFacet =>
                                                          funnelbackFacet.name ===
                                                          facet.funnelbackName
                                                  )
                                                : []
                                        }
                                        update={props.update}
                                    />
                                );
                            case 'selectbox':
                                return (
                                    <SelectboxFilter
                                        key={facet.meta}
                                        facet={facet}
                                        query={props.query}
                                        responseFacet={
                                            props.response &&
                                            props.response.facets
                                                ? props.response.facets.filter(
                                                      funnelbackFacet =>
                                                          funnelbackFacet.name ===
                                                          facet.funnelbackName
                                                  )
                                                : []
                                        }
                                        update={props.update}
                                    />
                                );
                            case 'toggle':
                                return (
                                    <ToggleFilter
                                        key={facet.meta}
                                        facet={facet}
                                        query={props.query}
                                        responseFacet={
                                            props.response &&
                                            props.response.facets
                                                ? props.response.facets.filter(
                                                      funnelbackFacet =>
                                                          funnelbackFacet.name ===
                                                          facet.funnelbackName
                                                  )
                                                : []
                                        }
                                        update={props.update}
                                    />
                                );
                        }
                    } else {
                        return null;
                    }
                })}
            </fieldset>
        </div>
    );
}

finder__filters.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    response: PropTypes.object,
    update: PropTypes.object,
    clear: PropTypes.func,
    mobile: PropTypes.bool,
};

export default finder__filters;
