import { createElement, removeClass } from '../../util';
const className = 'slider';
let sliderChildren, sliderChildrenLength;

function initSlider(slider) {
    if (slider.children.length < 2) {
        removeClass(slider, className, false);
        return;
    }

    // slider items count
    sliderChildren = [...slider.children];
    sliderChildrenLength = sliderChildren.length;
    for (const sliderChild of sliderChildren.entries()) {
        if (sliderChild[0] < 1) {
            sliderChild[1].classList.add('slider__active-slide');
        } else {
            // only displays first slide and hides rest
            sliderChild[1].classList.add('slider__slide');
            /* generates controls */
            let sliderControlsWrap = createElement(
                'div',
                null,
                null,
                null,
                'slider__controls__wrap'
            );
            let sliderControls = createElement(
                'div',
                null,
                null,
                null,
                'slider__controls'
            );
            let sliderProgress = createElement(
                'div',
                null,
                null,
                null,
                'slider__controls__progress'
            );
            let sliderButtons = createElement(
                'div',
                null,
                null,
                null,
                'slider__controls__buttons'
            );

            // generates progress
            sliderProgress.appendChild(
                createElement(
                    'span',
                    '1',
                    null,
                    null,
                    'slide__controls__progress__active'
                )
            );
            sliderProgress.appendChild(
                createElement(
                    'span',
                    ' /',
                    null,
                    null,
                    'slide__controls__progress__separator'
                )
            );
            sliderProgress.appendChild(
                createElement(
                    'span',
                    sliderChildrenLength,
                    null,
                    null,
                    'slide__controls__progress__total'
                )
            );

            sliderControlsWrap
                .appendChild(sliderControls)
                .appendChild(sliderProgress);
            slider.appendChild(sliderControlsWrap);

            // generates buttons
            sliderButtons.appendChild(
                createElement(
                    'button',
                    null,
                    'Previous item',
                    true,
                    'fas',
                    'fa-arrow-left',
                    'slider__controls__buttons__prev',
                    'swiper-slider-arrow',
                    'arrow-left--btn-prev'
                )
            );
            sliderButtons.appendChild(
                createElement(
                    'button',
                    null,
                    'Next item',
                    null,
                    'fas',
                    'fa-arrow-right',
                    'slider__controls__buttons__next',
                    'swiper-slider-arrow',
                    'arrow-right--btn-next'
                )
            );

            sliderControls.appendChild(sliderButtons);
            slider
                .querySelectorAll('.slider__controls__buttons__prev')[0]
                .classList.add('slider__controls__buttons__disabled');

            // Adds event listener to buttons
            slider
                .querySelector('.slider__controls__buttons__prev')
                .addEventListener('click', function() {
                    handleSlideChange(-1, slider);
                });

            slider
                .querySelector('.slider__controls__buttons__next')
                .addEventListener('click', function() {
                    handleSlideChange(1, slider);
                });
        }
    }
}

function handleSlideChange(direction, slider) {
    let activeSlide = parseInt(
        slider.querySelectorAll('.slide__controls__progress__active')[0]
            .innerText
    );
    let newSlide = activeSlide + direction;

    // ensures you don't slide past first and last slide
    if (newSlide !== 0 && !(newSlide > sliderChildrenLength)) {
        // udpates progress
        slider.querySelectorAll(
            '.slide__controls__progress__active'
        )[0].innerHTML = newSlide;

        sliderChildren.forEach(function(item, i) {
            //displays new slide
            if (i == newSlide - 1) {
                item.classList.remove('slider__slide');
                item.classList.add('slider__active-slide');
            } else {
                item.classList.remove('slider__active-slide');
                item.classList.add('slider__slide');
            }
        });
        // disables next & prev buttons when reaching beginning or end of slides
        if (newSlide == 1) {
            slider
                .querySelectorAll('.slider__controls__buttons__prev')[0]
                .classList.add('slider__controls__buttons__disabled');
            slider
                .querySelectorAll('.slider__controls__buttons__prev')[0]
                .setAttribute('disabled', true);
        } else {
            slider
                .querySelectorAll('.slider__controls__buttons__prev')[0]
                .classList.remove('slider__controls__buttons__disabled');
            slider
                .querySelectorAll('.slider__controls__buttons__prev')[0]
                .removeAttribute('disabled');
        }

        if (newSlide == sliderChildrenLength) {
            slider
                .querySelectorAll('.slider__controls__buttons__next')[0]
                .classList.add('slider__controls__buttons__disabled');
            slider
                .querySelectorAll('.slider__controls__buttons__next')[0]
                .setAttribute('disabled', true);
        } else {
            slider
                .querySelectorAll('.slider__controls__buttons__next')[0]
                .classList.remove('slider__controls__buttons__disabled');
            slider
                .querySelectorAll('.slider__controls__buttons__next')[0]
                .removeAttribute('disabled');
        }
    }
}

export default {
    launchFn: initSlider,
    launchQuery: `.${className}`,
};
